@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

.pageheader {
  @include shadow;

  overflow: hidden;
  position: relative;
  text-align: center;
  h1 {
    font-size:2.5rem;
    letter-spacing: 1.33px;
    margin:0 0 4px 0;
  }
  p {
    margin-top:13px;
  }
}

.pageheader__spiral {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}


.pageheader__spiral__pattern {
  $spiral: "intro/spiral.png";

  animation: rotating 20s linear infinite;
  background: image-url($spiral);
  height: image-height($spiral);
  transform-origin: 50%;
  width: image-width($spiral);
}
.pageheader__container {
  position: relative;
  z-index:2;
  padding:54px 0 0;
  align-items: center;
}

.pageheader__content,
.pageheader__characters {
  position: relative;
}

.pageheader__content {
  padding:20px 0;
}

@media #{$medium-up} {
  .pageheader {
    h1 {
      font-size:3rem;
    }
  }
}


@include reduce-motion {
  .pageheader__spiral__pattern {
    animation: none;
  }
}