@mixin font-Sniglet() {
  font-family: 'Sniglet', 'Open sans', 'Helvetica', sans-serif;
}

@mixin  font-Dosis() {
  font-family: "Dosis", "Open sans", "Helvetica", sans-serif;
}

@mixin  font-Lato() {
  font-family: "Lato", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

@mixin  font-Viga() {
  font-family: "Viga", sans-serif;
}

// Usage
// http://codepen.io/macx/full/qmkje
//
// .dark-on-light {
//   @include font-smoothing(off);
// }
//
// .light-on-dark {
//   @include font-smoothing(on);
// }
@mixin font-smoothing($value: on) {
  @if $value == on {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  @else {
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: auto;
  }
}

@mixin shadow($yOffset: 2px, $opacity: .5) {
  text-shadow: 0 $yOffset 0 rgba(255,255,255,$opacity);
}