@keyframes dropin {
  0% {
    transform: translateY(-120%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes charHover {
  0% {
    transform: translateY(-10px);
  }

  50% {
    transform: translateY(0px);
  }

  100% {
    transform: translateY(-10px);
  }
}


@keyframes charShadow {
  0% {
    transform: scaleX(1.1);
    opacity:.9;
  }

  50% {
    transform: scaleX(1);
    opacity:1;
  }

  100% {
    transform: scaleX(1.1);
    opacity:.9;
  }
}
