
@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

.hero {
  transition:all .5s ease;
  position: relative;
  h1, .h1 {
    @include font-Sniglet();
    @include shadow(4px);
    font-size:15vw;
    letter-spacing: 1px;
    margin:0;
    color: get-color('dark-blue');
    -webkit-font-smoothing: antialiased;
    font-weight: 800;
  }
}


.hero-wrapper {
  overflow: hidden;
  position: relative;
}

.hero__bg__container,
.hero__bg {
  position: absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
}

.hero__bg--pink {
  // background: linear-gradient(to bottom, #FDBC91 0%, #F692A1 100%);
  background-image: linear-gradient(to bottom, #FFB6D9 0%, #FCD454 100%);
}
.hero__bg--puffy {
  background-image: linear-gradient(to bottom, #CBB0FF 0%, #CCFFFD 100%);
}
.hero__bg--powerful {
  background-image: linear-gradient(to bottom, #FFAE29 0%, #FCD555 100%);
}
.hero__bg--final {
  background-image: linear-gradient(to bottom, #CCFFFD 0%, #FFB6D9 100%);
}

.hero__frame {
  position: absolute;
  width:100%;
  top:50%;
  transform:translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index:2;
  flex-wrap:wrap;
  opacity: 0;
  .img-max {
    display: block;
    margin-left:auto;
    margin-right:auto;
    max-width:100%;
  }
}

.hero__frame__img {
  flex-basis:70%;
  width:70%;
  max-width:70%;
}

.hero__frame__txt {
  flex-basis:100%;
  width:100%;
  max-width:100%;
  text-align: center;
}


.hero-splat {
  position: absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
  z-index:10;
  display: none;
}

.hero-splat__img {
  position: absolute;
  left:50%;
  top:50%;
  transform:translate(-50%,-50%);
  opacity: 0;

  img {
    display: block;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }
}
.hero-splat__img--incoming {
  transform:translate(-50%,-50%) scale(0);
}

//Particle system styles
.hero__particlesystem {
  height: 100%;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  z-index: 2;
}

.hero__particlesystem__particle {
  $img: "intro/star-glow.png";

  background: image-url($img) no-repeat;
  background-size: contain;
  background-position: 50%;
  height: 50px;
  left: 50%;
  position: absolute;
  top: 50%;
  width: 50px;
  opacity: 0;
}

//background spiral
.hero__spiral {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.hero__spiral__pattern {
  animation: rotating 20s linear infinite;

  $spiral: "intro/spiral.png";
  background: image-url($spiral);
  height: image-height($spiral);
  transform-origin: 50%;
  width: image-width($spiral);
}

.hero__final {
  position: relative;
  z-index:5;
  opacity: 0;
}

.audio-btn {
  width: 70px;
  height: 40px;
  padding-right: 10px;
  position: absolute;
  padding-left: 5px;
  background-color: #fff;
  box-shadow: 0px 2px 1px rgba(0,0,0,.15);
  right: 0;
  top: 70px;
  border: none;
  transform: translateX(70px);
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  display: block;
  cursor: pointer;
  z-index: 90;
  transition: transform .15s ease-in-out;
  .is-loaded & {
    transform: translateX(10px);
  }

  &:hover {
    transform: translateX(0px);
    transition-delay: 0;
  }
}

.audio-btn__status {
  $img: 'home/sound-icons-off.svg';
  background-color: transparent;
  background-image: image-url($img);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 25px 25px;
  width: 100%;
  height: 100%;
  display: block;

  .is-playing & {
    $img: 'home/sound-icons-on.svg';
    background-image: image-url($img);
  }
}

.hero__content {
  position: relative;
  text-align: center;
  @include font-Dosis();
  font-weight: 700;
  letter-spacing: 0.5px;
  padding-top:70px;

  span.img {
    display: block;
    position: relative;
    max-width:220px;
    width:100%;
    margin:0 auto;
    padding-top: 10px;
  }

  h1, .h1 {
    font-size:1.5rem;
    font-weight:700;
    line-height: 1.5;
    letter-spacing: 0.5px;
    text-shadow: 0 2px 0 rgba(255,255,255,0.50);
    img  {
      position: relative;
      z-index: 2;
      display: block;
      max-width:100%;
      margin:0 auto;
      // margin-left: 50%;
      // transform: translateX(-50%);
    }
  }
  p {
    font-size:1rem;
  }
}
.hero__image-group {
  width: 100%;
  margin-top: 8rem;
  position: relative;
  $kirby:'kirby-star2.png';
  $metaknight:'metaknight.png';
  $dedede:'dedede.png';
  $waddledee:'waddledee.png';



  .kirby {
    position: absolute;
    top: -6rem;
    left: 12.5rem;
    z-index: 1;
  }
  .metaknight {
    position: absolute;
    top: -15rem;
    left: 24.5rem;
  }
  .dedede {
    position: absolute;
    top: -1rem;
    left: 0;
  }
  .waddledee {
    position: absolute;
    top: -11rem;
    left: 5rem;
  }
}
@media #{$small-only} {
  .hero__container {
    padding: 0;
  }
  .hero__image-group {
    top: 7rem;
    height: 10rem;
    width: 275px;
    margin-left: 50%;
    transform: translateX(-50%);

    .kirby {
      width: 215px;
      height: auto;
      top: -10rem;
      // left: 3.5rem;
      left: 20%;
    }
    .metaknight {
      width: 150px;
      height: auto;
      // top: -15rem;
      // left: 9rem;
      top:-14rem;
      left:60%;
    }
    .dedede {
      // width: 108px;
      height: auto;
      top: -7rem;
      // left: -1.5rem;
      width: 118px;
      left: calc(5% - 50px);
    }
    .waddledee {
      // width: 50px;
      height: auto;
      // top: -10.5rem;
      // left: 2rem;

      width: 90px;
      top: -12rem;
      left: calc(18% - 40px)
    }
  }
}

@media #{$medium-only} {
  .hero__image-group {
    height: 450px;
    top: 6rem;
    left: 55%;
    transform: translateX(-50%);
    .kirby {
      width: 408px;
      height: auto;
      bottom: 0;
      // left: 11.5rem;
      left:calc(27% - 30px);
    }
    .metaknight {
      width: 279px;
      height: auto;
      // top: -15rem;
      // left: 22rem;
      top:-12rem;
      left: calc(65% - 70px);
    }
    .dedede {
      width: 234px;
      height: auto;
      top: -2rem;
      // left: 0rem;
      left: calc(10% - 90px);
    }
    .waddledee {
      width: 181px;
      height: auto;
      top: -10.5rem;
      // left: 5rem;
      left: calc(15% - 50px);
    }
  }
}

@media #{$medium-up} {
  .hero__container {
    padding: 3rem 0 0;
  }

  .hero__content {
    h1, .h1 {
      font-size:1.875rem;
    }
    span.img {
      padding-top:20px;
      max-width:350px;
    }
    p {
      font-size:1.125rem;
    }
  }
}
@media #{$large-only} {
  .hero__image-group {
    top: 6rem;
    left: 30%;
    transform: translateX(-50%);
    .kirby {
      width: 400px;
      height: auto;
      bottom: 0;
      left: 10.5rem;
    }
    .metaknight {
      width: 250px;
      height: auto;
      top: -14rem;
      left: 20rem;
    }
    .dedede {
      width: 225px;
      height: auto;
      top: -1rem;
      left: 0rem;
    }
    .waddledee {
      width: 165px;
      height: auto;
      top: -10.5rem;
      left: 5rem;
    }
  }
}

@media #{$large-up} {
  .hero-wrapper {
    min-height:603px;
  }
  .hero__frame {
    flex-wrap:nowrap;
    height:100%;
    left:50%;
    margin:0 auto;
    max-width: 1140px;
    top:0;
    transform:translateX(-50%);
    width:100%;
    h1, .h1 {
      font-size:7.5rem;
    }
  }

  .hero__frame__img,
   .hero__frame__txt {
    flex-basis:auto;
    max-width:none;
    text-align: left;
    width:auto;
  }

  .hero__frame--1 {
    .hero__frame__txt {
      align-self:flex-end;
      margin:0 0 0 -5%;
      padding-bottom:95px;
      color: get-color('dark-blue');
      @include font-Sniglet();
      @include font-smoothing();
    }
  }
  .hero__frame--2 {
    .hero__frame__txt {
      order:0;
    }
    .hero__frame__img {
      order:1;
      padding-left:3%;
    }
  }

  .hero__frame--3 {
    justify-content: flex-start;
    .hero__frame__img {
      flex-shrink:0;
      flex-grow:0;
      margin-left:-3%;
    }
    .hero__frame__txt {
      position: absolute;
      bottom:0;
      left: grid-calc(3,12);

    }
  }
  .hero__final {
    position: absolute;
    top:50%;
    left:50%;
    width:100%;
    transform:translate(-50%,-50%);

  }

  .hero__content {
    padding-top:0;
    span.img {
      max-width:none;
      width:auto;
    }
  }
  .hero__container {
    padding: 7rem 0 4rem 2.5rem;
  }
}

@media #{$xlarge-up} {
  .hero__image-group {
    margin-top: 10rem;
  }
}

@include reduce-motion {
  .hero__spiral__pattern {
    animation: none;
  }

  .audio-btn {
    opacity: 0;
  }

  .is-loaded {
    .audio-btn {
      opacity: 1;
      transition: transform 0s, opacity .3s;
    }
  }
}