.page-news-and-videos {
  .pageheader {
    background-image: linear-gradient(-180deg, #F6A8B3 0%, #FDD6BD 100%);
    &.loaded {
      .knight,
      .kirby {
        transform:scale(1) translate3d(0,0,0);
      }
      .cat {
        transform:scale(1);
      }
    }
    .pageheader__container {
      padding: 55px 0;
    }
    .pageheader__content {
      padding: 55px 11px 0px 11px;
    }
    .pageheader__characters {
      display: flex;
      justify-content: center;
      margin:52px 0 -58px;
    }
    .knight {
      position: absolute;
      top:-13%;
      right:10%;
      width:32.14457831%;
      transform:scale(0) translate3d(-950%,20%,0);
      transition: .4s transform .1s ease-out;
    }
    .kirby {
      position: relative;
      z-index:2;
      width: 56.15060241%;
      left: -10%;
      transform: scale(0) translate3d(-100%,-20%,0);
      transition:.4s transform ease-out;
    }
    .cat {
      position: absolute;
      top: -15%;
      left: 9%;
      width:23.75301205%;

      transform: scale(0);
      transition:.2s transform .5s ease-out;
    }
  }
  .section--cards {
    background-image: linear-gradient(-180deg, #7641D4 0%, #C57CB1 100%);
    padding:60px 0 205px 0;
    position: relative;
  }

  .bg {
    $img:'bg_starpattern-white.png';
    content:'';
    position: absolute;
    background:image-url($img) repeat center center;
    width:100%;
    height:100%;
    top:0;
    left:0;
    opacity: .1;
  }
  
  .cta {
    margin: 0 auto !important;
    .load-more {
      display: block;
      margin-left: auto;
      margin-right: auto;
      max-width: 500px;
      width: 100%;
    }
    .load-more--hidden {
      display: none;
    }
  }
  .section--cards {
    ul {
      justify-content: flex-start;
      padding: 0px;
      li {
        list-style: none;
      }
    }

    .news-article__modal-content {
      display: none;
    }
  }

  .customer__support {
    position: relative;
    z-index: 10;
    margin-bottom: 10px;
    margin-top: 60px;
  }
}

@media #{$medium-only} {
  .news-item {
      flex-basis: 50%;
      width: 50%;
      max-width: 50%;
    }
}


.page-news-and-videos {
  .header-main {
    filter: drop-shadow(0 6px 5px rgba(0,0,0,0.20));
    text-align: center;
  }

  .header-main__title {
    color: white;
    display: inline-block;
    filter: drop-shadow(0 6px 5px rgba(0,0,0,0.24));
    position: relative;
    transform: translateY(-50%);

    h1 {
      background-color: #961115;
      background-image: linear-gradient(90deg, #961115 0%, #C4161C 48%, #961115 97%);
      margin: 0;
      padding: 12px 110px;

      @supports (clip-path: polygon(0% 0%, 0% 0%, 0% 0%)) {
        --angle: 16px;

        clip-path: polygon(
          0 0,
          100% 0,
          calc(100% - var(--angle)) 50%,
          100% 100%,
          0 100%,
          var(--angle) 50%
        );
      }
    }
  }

  .section--news {
    margin-bottom: 72px;
    min-height: 100vh;
    padding-top: 16px;
    position: relative;
  }

  .load-more {
    // The reason this button is absolutely positioned, is because
    // we are setting min-height (100vh) on the parent content div,
    // and if we don't anchor this button to the bottom, we'll see it
    // for a second before the news items are loaded.
    // Could just hide this with JS until loaded, but this is totally fine.
    position: absolute;
    bottom: -80px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.header-main__scene {
  // --curve-factor equates to a negative margin value,
  // and essentially contributes to a big ol' width on the element.
  --curve-factor: 60vw;

  align-items: center;
  display: flex;
  height: 220px;
  justify-content: center;
  mask-image: radial-gradient(
    ellipse at 50% 20%,
    rgba(0,0,0,1) 70.5%,
    rgba(0,0,0,0) 70.5%
  );
  margin: 0 calc(var(--curve-factor) * -1);
  position: relative;
}

.header-main__scene-bg {
  background-color: #FBBB37;
  background-image: image-url("news/scene-bg.jpg");
  background-position: 50% 100%;
  background-size: cover;
  height: 100%;
  position: absolute;
  top: 0;
  left: var(--curve-factor);
  width: calc(100% - var(--curve-factor) * 2);
}

.main-header__scene-logo {
  flex: 0 1 200px;
  margin: -20px 0 0;
  position: relative;
  z-index: 1;

  img {
    display: block;
    width: 100%;
  }
}

.main-header__scene-character {
  align-self: flex-start;
  margin-top: -12px;
  position: relative;
}

.button-js-clone {
  display: none;
}

.button--back {
  align-items: center;
  border: 6px solid #fff;
  border-radius: 3em;
  box-shadow: none;
  display: flex;
  margin: 0 auto;
  position: relative;
  transform: translateY(-50%);
  width: 300px;
  z-index: 1;

  &:last-child {
    transform: translateY(50%);
  }

  .button__text {
    span {
      padding: 2px 0 4px 0;
      width: 290px;
    }
  }

  .button__arrow {
    display: inline-block;
    margin-right: 12px;
    position: absolute;
    left: 20px;
    top: 14px;
    z-index: 4;
  }
}

@media (hover: hover) {
  .button--back {
    .button__arrow {
      position: absolute;
      transform: translateX(0);
      transition: transform .2s;
    }

    &:hover {
      .button__arrow {
        transform: translateX(-5px);
      }
    }
  }
}

@media #{$small-only} {
  .page-news {
    .header-main__title {
      h1 {
        font-size: 2rem;
        padding: 8px 50px;
      }
    }

    .section--news {
      padding-bottom: 64px;
    }
  }
}

@media #{$medium-up} {
  .header-main__scene {
    --curve-factor: 30vw;

    height: 310px;
  }
  .page-news-and-videos {
    .pageheader {
      .pageheader__characters {
        display: flex;
        justify-content: center;
        margin:39px 0 -58px;
      }
    }
  }

  .main-header__scene-logo {
    flex: 0 0 auto;
    margin-right: -1%;
  }
}
