.skippers {
  left: calc(50% + 100px);
  // overflow: hidden;
  position: absolute;
}

.skippers__item {
  opacity: 0;
  pointer-events: none;

  &:focus {
    opacity: 1;
    pointer-events: all
  }

  &:focus {
    @include focus-styles;
  }
}

#skippers-main {
  left: 0;
  position: fixed;
  top: 0;
  pointer-events: none;
}