.amiibo__cards {
  display: -ms-flexbox;
  display: -webkit-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: center;
  -webkit-flex-pack: center;
  -webkit-box-pack: center;
  justify-content: center;
  list-style-type: none;
  padding: 0;
}
.amiibo__cardcontainer {
  max-width: 300px;
  min-width: 150px;
  padding: 10px;
  width: 50%;
  position: relative;
  z-index: 1;
}
.amiibo__card {
  display: block;
  text-decoration: none;
  cursor: pointer;
  border: 3px solid get-color('white');
  background-color: get-color('acid');
  border-radius: 20px;
  position: relative;
  box-shadow: 4px 4px 0 get-color('shadow');
  transition: all 0.8s ease;
  z-index: 2;
  padding: 100% 10px 2rem;
  color: #1423A5;
   .name {
    color: #1423A5;
   }

  &:hover,
  &:focus {
    //box-shadow: 0 2px 4px 0 get-color('shadow-dark'), 4px 4px get-color('shadow');
    transform: scale(1.025);
    transition: all 0.3s ease;
  }
  img {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
  }
  .name {
    @include font-Dosis();
    @include shadow();

    font-size: rem-calc(22px);
    font-weight: 800;
    letter-spacing: 0.5px;
    position: relative;
    top: 0.5rem;
    white-space: nowrap;
  }
}

@media #{$small-only} {
  .amiibo__card {
    img {
      top: 40%;
    }
  }
}

@media #{$medium-only} {
  .amiibo__cardcontainer {
    &:nth-of-type(2n){
      @include slide-effect(false)
    }
  }
}
@media #{$large-up} {
  .amiibo__cardcontainer {
    width: 25%;
    &:nth-of-type(4n + 4), &:nth-of-type(4n + 3){
      @include slide-effect(false)
    }
  }
  .amiibo__cards {
    justify-content: space-around;
  }
}

@include reduce-motion {
  .amiibo__card {
    transition: none;
  }
}