//span 100% (1-col)
.playframe {
  position: relative;
  z-index: 2;
  width: 100%;
  flex-wrap: wrap;
  padding-bottom: 10%;

  h2 {
    @include shadow();
    text-align: center;
    // padding: 4rem 0 1rem;
    text-align: center;
    padding:40px 0 0;
  }
}
.playframe__container {
  position: relative;
  padding-bottom:12%;
}

.playframe__hero {
  position: absolute;
  width:35%;
  top:0;
  left:0;
  img {
    width: 100%;
    display:none;
  }
}
.playframe__frame {
  position: relative;
  width:100%;
  height:0;
  padding-bottom:200%;

  iframe {
    border:none;
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
  }
}

@media #{$medium-up} {
  .playframe {

    h2 {
      margin-left:auto;
      margin-right:0;
      padding-right:10px;
      width:60%;
    }
  }
  .playframe__hero {
    img {
      display: block;
    }
  }
}

//span 100% (4-col)
@media only screen and (min-width: 43.125em) {
  .playframe__frame {
    padding-bottom:percentage(411/738);
    max-width: 990px;
    margin: 0 auto;
  }
}

body.is-ie11  {
  @media only screen and (min-width: 690px) and (max-width: 714px) {
    .playframe__frame {
      padding-bottom: 200%;
    }
  }
}

body.is-edge {
  @media only screen and (min-width: 690px) and (max-width: 707px) {
    .playframe__frame {
      padding-bottom: 200%;
    }
  }
}


@media #{$large-up} {
  .playframe {
    padding-bottom: 6rem;
  }
  .playframe__container {
    flex-basis: 100%;
    max-width:100%;
    padding-top:0;
    padding-bottom:0;
    width:100%;
  }
  .playframe__hero {
    max-width:300px;
    left:3%;
  }
}

//snap to left hand side at 66% (4-col)
@media only screen and (min-width: 66.75em) {
  .playframe {
    justify-content: flex-end;
  }
  .playframe__hero {
    width:40%;
    max-width:none;
    position: absolute;
    display: block;
    left: -3%;
    top: 8%;
  }
  .playframe__container {
    flex-basis: 66.66667%;
    width: 66.66667%;
    max-width: 66.66667%;
    h2 {
      margin-left:auto;
      margin-right:auto;
      text-align: center;
      width:auto;
    }
  }
}


@media #{$xlarge-up} {
  .playframe__hero {
    left:-10%;
  }
  .playframe__container {
    flex-basis: 75%;
    width: 75%;
    max-width: 75%;
  }
}
