.nclood-nav__container {
  position: relative;
  z-index: 99;
}

.nav-kirby {
  height:50px;
  position: absolute;
  text-align: right;
  width:100%;
  z-index:30;
  color: get-color('dark-blue');

  &:before {
    background-color: get-color('white');
    content:'';
    height: 100%;
    left:0;
    opacity: .75;
    position: absolute;
    top:0;
    width:100%;
  }
}

.nav-sticky .nav-kirby {
  position: fixed;
  top:0;

  &::before {
    opacity: 0.95;
  }
}

.nav-kirby__logo {
  position: absolute;
  top:50%;
  left:10px;
  margin-top:-20px;
}

#kirby-nav__toggle {
  display: none;
}

.nav-kirby__menu {
  display: inline-block;
  position: relative;
  padding-right:10px;
  cursor:pointer;
  > span {
    color:transparent;
    font: 0/0 a;
    width:17px;
    height:17px;
    background-repeat:no-repeat;
    background-position:center center;
  }
}

.nav-kirby__closed {
  $img: 'nav/nav-menu.svg';

  background-image:image-url($img);
  display: block;
}
.nav-kirby__open {
  $img: 'nav/nav-close.svg';

  background-image:image-url($img);
  display:none;
}

.nav-kirby__list {
  @include font-Dosis();
  @include shadow;

  display: block;
  font-size:1.375rem;
  font-weight:700;
  letter-spacing: 1px;
  line-height: 1.2727272727;
  list-style:none;
  margin:0;
  opacity: 0;
  padding:0;
  text-align: center;
  visibility: hidden;
}

.nav-kirby__link {
  margin: 0 12px;
}

.nav-kirby__link a {
  display: inline-block;
  text-decoration: none;
  padding: 13px 23px;
  position: relative;

  &:before,
  &:after {
    $img: 'nav/nav-star_2x.png';

    background: image-url($img) no-repeat left top;
    background-size:100%;
    content:'';
    height:15px;
    margin-top:-7.5px;
    opacity: 0;
    position: absolute;
    top:52%;
    transform:scale(0);
    transition:transform .2s ease-out, opacity .1s ease-out, visibility .2s ease-out;
    visibility: hidden;
    width:15px;
  }
  &:before {
    left:0;
  }
  &:after {
    right:0;
  }

  &.is-active,
  &:hover {
    &:before,
    &:after {
      opacity: 1;
      visibility: visible;
      transform:scale(1);
    }
  }

  #wrap &:focus {
    box-shadow: inset 0 0 3px 3px #CCFFFD, 0 0 3px 3px #CCFFFD;
    outline: 2px dashed #1423A5;
    outline-offset: -2px;
  }
}
.nav-kirby__link-logo {
  display: none;
}

/* onMenu toggle */
#kirby-nav__toggle:checked ~ .nav-kirby__menu {
  .nav-kirby__closed {
    display: none;
  }
  .nav-kirby__open {
    display: block;
  }
}

#kirby-nav__toggle:checked ~ .nav-kirby__list {
  opacity: 1;
  position: absolute;
  top:50px;
  visibility: visible;
  width:100%;

  &:before {
    background-color: rgba(255,255,255,1);
    content:'';
    height:100%;
    left:0;
    // opacity: 0.75;
    position: absolute;
    top:0;
    width:100%;
  }
}

.nav-sticky {
  #kirby-nav__toggle:checked ~ .nav-kirby__list {
    &:before {
      opacity: 0.95;
    }
  }
}

@media #{$medium-up} {
  .nav-kirby {
    height:auto;
    &:before {
      transition:opacity .2s ease-in;
    }
  }
  .nav-kirby__logo,
  .nav-kirby__menu {
    display: none;
  }

  .nav-kirby__list {
    align-items:center;
    display: flex;
    margin:0 auto;
    opacity: 1;
    position: relative;
    justify-content: space-around;
    visibility: visible;
    // max-width: 340px; //when only two nav items.
    max-width:820px;
    .page-home & {
      max-width:650px;
    }
  }
  .nav-kirby__link a {
    display: block;
    text-decoration: none;
    padding: 13px 23px;
    position: relative;
  }

  .nav-kirby__link-logo {
    display: block;
    margin: 0px 40px;
    .page-home & {
      display: none;
    }
  }
}

@include reduce-motion {
  .nav-kirby__link a {
    &:before,
    &:after {
      opacity: 0;
      transform: scale(1);
    }

    &:hover,
    &.is-active {
      &:before,
      &:after {
        opacity: 1;
      }
    }
  }
}

.hamburgerOpen {
  background: #fff;
}