.slide-effect {
  @include slide-effect()
}

.slide-effect--right {
  @include slide-effect(false);
}

.slide-effect--center {
  transform: translateX(0);
}

.vp-on-30 {
  .slide-effect,
  .slide-effect--right {
    @include slide-effect-in();
  }
}

@include reduce-motion {
  .slide-effect,
  .slide-effect--right,
  .slide-effect--center {
    opacity: 0;
    transition: opacity .3s linear 0s; 
  }

  .vp-on-30 {
    .slide-effect,
    .slide-effect--right,
    .slide-effect--center {
      opacity: 1;
    }
  }
}
