.featured-games__list {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none;
  padding: 0 0 5px 0;
  width: 100%;
  -webkit-overflow-scrolling: touch;
  overflow-y: hidden;
  overflow-x: scroll;

  a {
    text-decoration: none;
  }

  .wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;

    // was small-only
    flex-wrap: nowrap;
    width: auto;
    justify-content: space-between;
    padding-left: 5px;
    padding-right: 5px;
  }

  @media #{$large-up} {
    overflow-y: visible;

    overflow-x: visible;

    .wrapper {
      flex-wrap: wrap;
      width: 100%;
    }
  }
}

.featured-games__item {
  z-index: 19;
  position: relative;
  transition: transform .15s ease-in-out;
  text-align: center;
  @include font-Dosis();
  font-size: rem-calc(14);
  width: 86.5vw;
  text-align: left;
  box-shadow: none;

  .button {
    text-decoration: none;
    display: block;
    position: relative;
    overflow: hidden;
    z-index: 1;
    left: 1px;
    text-align: center;
    height: 46px;
    background-color: #7197FF;
    border: 3px solid get-color('dark-blue');
    letter-spacing: 0.045454545em;
    pointer-events: none;
    text-decoration: none;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    transition: all 0.5s ease;
    line-height: 35px;

    &:hover {
      transform: scale(1.0325);
      transition: all 0.2s ease;
    }
    img {
      position: absolute;
      left: calc(100% - 35px);
      z-index: 1;
      top: 50%;
      transform: translateY(-50%);
      filter: drop-shadow(0 2px get-color('shadow'));
    }

    &::before{
      background-color: rgba(255,255,255, 0.1);
      content: '';
      height: 55px;
      width: 100%;
      border-radius: 50%;
      position: absolute;
      left: 0;
      top: -2.1rem;
      z-index: 1;
    }
    &::after {
      background-color: #2457E4;
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      border-bottom: 2px solid #1646C9;
      position: absolute;
      top: 2px;
      left: 0;
      content: '';
      height: calc(100% - 1px);
      width: 100%;
      z-index: -1;
    }

    span {
      color: get-color('white');
      font-weight: 700;
      font-size: rem-calc(22px);
      @include text-Shadow();
      top: 13%;
      position: relative;
      z-index: 2;
    }
  }

  @media #{$medium-only} {
    // width: 30.5vw;
    width: 32vw;
    flex: 1 1 32vw;
    min-width: 32vw;
  }
  @media #{$small-only} {
    // width: 30.5vw;
    width: 86.5vw;
    flex: 1 1 86.5vw;
    min-width:86.5vw;;

  }
}

.featured-games__item-wrap {
  display: block;
  background-color: #fff;
  margin-left: 3px;
  margin-right: 3px;
  padding-top: 1px;
  border-radius: 12px;
  left: 0;
  box-shadow: 4px 4px get-color('shadow');
  position: relative;

  @media #{$large-up} {
    background-color: transparent;
    margin: 0;
  }
}

.featured-games__image {
  width: 100%;
  margin-top: 0;
  transform: scale(.98);
  transition: transform .10s ease-out;
  border: 3px solid get-color('white');
  border-radius: 12px;
  box-shadow: none;

  img {
    width: 100%;
    display: block;
    border-radius: 12px;
  }
}

.games__copy, .game-sort__toggle {
  color: get-color('dark-blue');
}

.esrb_container {
  display: flex;
  justify-content: center;
}

.featured-games__platform-tag-wrap {
  padding: 0px 0 0px 8px;
  color: get-color('red');
  font-size: rem-calc(14px);
  font-weight: 700;

  .platform--wiiu {
    color: #0096c8;
  }
  .platform--nintendo3ds {
    color: #ce181e;
  }
  .platform--switch {
    color: #e60012;
  }

  span {
    font-weight: 600;
    letter-spacing: 0.071428571em;
    padding-left: 12px;
  }

  @media #{$large-up} {
    padding: 0px 0 8px 8px;

    span {
      background-color: #fff;
      padding: 6px 12px 6px 12px;
      margin-bottom: 5px;
      display: inline-block;
      margin-right: 25px;
      border-bottom-left-radius: 12px;
      border-bottom-right-radius: 12px;
      box-shadow: 0px 2px get-color('shadow');
    }
  }
}

.featured-games__hidden {
  background: #fff;
  z-index: 20;
  width: 100%;
  position: static;
  top: 0;
  text-align: left;
  left: 0px;
  border-radius: 12px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  padding:8px 20px 80px;

}
.featured-games__details-button {
  position: absolute;
  bottom: 20px;
  width:100%;
  padding:0 20px;
  left:0;
}


.featured-games__title {
  @include font-Dosis();

  color: #324C95;
  letter-spacing: 0.055555556em;
  font-size: rem-calc(18);
  font-weight: 800;
}

.featured-games__cta {
  text-decoration: none;
  display: block;
  text-align: center;
  height: 46px;
  line-height: 46px;
  // background-color: get-color('gold');
  width: 100%;
  margin-top: 16px;
  font-weight: bold;
  @include font-Dosis();
  font-size: rem-calc(22);
  @include text-Shadow();

  &:hover {
    color: #fff;
  }
}


// HOVER STATE

@media #{$large-up} {
  .featured-games__item {
    width: 33.3333%;
    background: transparent;

    .featured-games__item-wrap {
      margin-left: 6px;
      margin-right: 6px;
    }

    .featured-games__image {
      box-shadow: 4px 4px get-color('shadow');
    }

    .featured-games__platform-tag-wrap {
      text-align: right;
    }

    .featured-games__hidden {
      display: none;
      left: -3px;
      position: absolute;
    }

    &:hover {
      box-shadow: 4px 4px get-color('shadow');
    }

    &:hover,
    &:focus-within {
      background-color: #fff;
      z-index: 21;
      // transform: scale(1.02);
      border: 3px solid get-color('white');
      border-radius: 12px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      margin-bottom: 5px;

      .featured-games__image {
        transform: scale(1) translateY(4px);
        border-radius: 8px;
        border: none;
        box-shadow: none;
        // padding-top: 6px;

        img {
          border-radius: 8px;
        }
      }

      .featured-games__platform-tag-wrap {
        text-align: left;
        padding-left: 14px;
        padding-bottom: 0;

        span {
          background-color: #fff;
          padding: 12px 6px 0px 0px;
          margin-bottom: 0px;
          display: inline-block;
          margin-right: 0;
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
          box-shadow: none;
        }
      }

      .featured-games__hidden {
        display: block;
        z-index: 22;
        top: 100.75%;
        border: 3px solid get-color('white');
        border-radius: 12px;
        width: calc(100% + 18px);
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        box-shadow: 4px 4px get-color('shadow');
        padding-top: 10px;
        left: -9px;

      }

      .featured-games__item-wrap {
        height: calc(100% - 12px);
      }
    }

    .featured-games__item-wrap {
      height: calc(100% - 12px);
      box-shadow: none;
    }
  }
}

@include reduce-motion {
  .featured-games__item {         
    .featured-games__image {
      transition: none;
    }
  }
}