@import url('https://fonts.googleapis.com/css?family=Dosis:400,600,700,800|Sniglet:400,800');


*,
*::before,
*::after {
  box-sizing: border-box;
}

#wrap {
  overflow: hidden;
}

html {
  font-size:16px;
}

h1, h2, h3 {
  @include font-Sniglet();
  @include font-smoothing();
  line-height: 1.27;
  color: get-color('dark-blue');
}

p {
  @include font-Dosis();
  font-size:1rem;
  font-weight: 700;
  letter-spacing: 0.5px;
  line-height: 1.5;
  margin-bottom: 0.8em;
  text-shadow: 0 2px 0 rgba(255,255,255,0.50);
  color: get-color('dark-blue');
}

em,
i {
  font-style: italic;
}

// Helpers
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

#wrap {
  *:focus {
    @include focus-styles;
  }
  
  .focus-within {
    &:focus-within {
      @include focus-styles;
    }
  
    *:focus {
      outline: none;
    }
  }
}

#wrap .modal .modal__focus-start {
  &:focus {
    @include focus-styles;
    outline: none;
    box-shadow: none;
  }
  
  .focus-within {
    &:focus-within {
      box-shadow: none;
    }
  
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
}

span.nobr {
  white-space: nowrap;
}

.font-fallback {
  @include font-Sniglet();

  transform: translateY(-0.5em);
  font-size: 0.5em;
  display: inline-block;
}

.text-center {
  text-align: center;
}

@each $color, $hex in $colors {
  .text-#{$color} {
    color: $hex;
  }
}

.text-shadow {
  text-shadow: 2px 2px 5px rgba(0,0,0,0.8);
}

.img-block {
  display: block;
}

.img-full {
  display: block;
  width: 100%;
}
.img-max {
  display: block;
  max-width:100%;
}
.hr--stars {
  $img: 'decoration-star.png';
  position: relative;
  background:image-url($img) no-repeat center center;
  width:image-width($img);
  height:image-height($img);
  border:none;
  margin:0 auto;
  overflow: visible;
  &:before,
  &:after {
    content:'';
    background:image-url($img) no-repeat center center;
    background-size:100%;
    position: absolute;
    top:50%;
    width:15px;
    height:15px;
    margin-top:-7.5px;
  }
  &:before {
    right:150%;

  }
  &:after {
    left:150%;
  }

}
/* Anchors */
a,
a:visited {
  font-family: inherit;
  color: inherit;
}

/* Headers */
h1,h2,h3,h4,h5 {
  margin: 0.65em 0 0.45em 0;

  &:first-child {
    margin-top: 0;
  }
}

.title-1 {
  font-size: 3.5rem;
}

.title-2 {
  @include font-Sniglet();
  font-size: rem-calc(48px);
  font-weight: 800;
  padding: 0 10px;
}

.title-3 {
  @include font-Sniglet();
  font-size:2rem;
  font-weight: 800;

}

.title-4 {
  @include font-Dosis();
  font-size: 1.375rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  &.large {
    font-size:3rem;
  }
}

.title-5 {
  font-size: 1rem;
  font-weight: bold;
}

@media #{$medium-up} {
  .title-3 {
    font-size:2.25rem;
  }
  p {
    font-size: 1.125rem;
  }
}

@media #{$large-up} {
}

@mixin full-absolute($inset: 0) {
  bottom: $inset;
  left: $inset;
  position: absolute;
  right: $inset;
  top: $inset;
}