a.button {
  color: get-color("white");
}
.button {
  @include font-Dosis();
  color: get-color("white");
  font-weight: 700;
  font-size: rem-calc(22px);
  border-radius: 25px;
  display: block;
  overflow: hidden;
  padding: 2px 0;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: all 0.5s ease;
  border: solid 3px transparent;
  z-index: 1;

  &:hover,
  &:focus {
    transform: scale(1.0325);
    transition-duration: 0.2s;
  }
  // img {
  //   position: absolute;
  //   left: calc(100% - 35px);
  //   z-index: 1;
  //   top: 50%;
  //   transform: translateY(-50%);
  //   filter: drop-shadow(0 2px get-color('shadow'));
  // }

  &::before {
    content: "";
    height: 50%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;

    z-index: 1;
  }
  &::after {
    content: "";
    border-radius: 0 0 3em 3em;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 50%;
    width: 100%;
  }

  span {
    @include text-Shadow();
    display: block;
    position: relative;
    z-index: 2;
    padding: 4px 0 9px 0;
    border-radius: 25px;

    &:before {
      background-color: rgba(255, 255, 255, 0.1);
      content: "";
      height: 55px;
      width: 100%;
      border-radius: 50%;
      position: absolute;
      left: 0;
      bottom: 40%;
      z-index: 1;
    }
  }
}
.button--arrow {
  span {
    &:after {
      $img: "arrow.png";
      content: "";
      background: image-url($img) no-repeat left top;
      width: image-width($img);
      height: image-height($img);
      position: absolute;
      right: 25px;
      margin-top: image-height($img) / 2 * -1;
      top: 50%;
      filter: drop-shadow(0 2px get-color("shadow"));
    }
  }
}
.button--arrow--left {
  span {
    &:after {
      transform: scaleX(-1);
      left: 25px;
      right: auto;
    }
  }
}

.button--blue {
  background-color: #7197ff;
  border-color: get-color("dark-blue");

  &:after {
    background-color: #1646c9;
  }
  span {
    background-color: #2457e4;
  }
}

.button--red {
  border-color: #ad2c3d;

  &:before {
    background: #f6cdcc;
  }
  &:after {
    background: #d27d7a;
  }
  span {
    background: #f49b98;
  }
}

.button--dark-red {
  border-color:#B11A45;

  &:before {
    background:#f5637e;
  }
  &:after {
    background: #cc3b5b;
  }
  span {
    background: #C72549;
  }
}


.button--coral {
  background-color: get-color("light-coral");
  border-color: get-color("dark-coral");

  &:after {
    background-color: #9e1534;
  }
  span {
    background-color: get-color("coral");
  }
}

.button--container--sm {
  max-width: 325px;
}

@include reduce-motion {
  .button {
    transition: none;
  }
}
