.BSParticle {
  display: block;
  height: 1px;
  left: 50%;
  position: absolute;
  pointer-events: none;
  top: 50%;
  width: 1px;
  z-index: 3;

  &::after {
    background: image-url('star-single.png');
    background-size: 100% 100%;
    content: '';
    display: block;
    height: 102px;
    position: absolute;
    transform: translate(-51px, -51px);
    width: 102px;
  }
}
.BSParticle--2::after {
  background: image-url('star-single-blue.png');
}
.BSParticle--3::after {
  background: image-url('star-single-green.png');
}
.BSParticle--4::after {
  background: image-url('star-single-pink.png');
}
.BSParticle--5::after {
  background: image-url('star-single-yellow.png');
}

@include reduce-motion() {
  .BSParticle {
    display: none;
  }
}