.pageheader__content {
  padding-left: 11px;
  padding-right: 11px;
}

.footer-main {
  background:#fff;
  color: get-color('black');
  padding: 20px 0 20px;
  position: relative;
  &::before {
    $img: 'footer-bg.png';
    animation: cloudAnimation 60s linear infinite;
    background:transparent image-url($img) left top;
    position: absolute;
    content: '';
    height: image-height($img);
    width: image-width($img) * 5;
    top: -60px;
    overflow-x: hidden;
    }
    .customer__support {
      font-family: Lato, Arial, Helvetica, sans-serif;
      font-size: 12px;
      text-decoration: none;
      &:hover {
        text-decoration:underline;
      }
    }
  }

.footer-main__lang {
  text-align: center;
  color: white;
  padding-top: 20px;
  padding-bottom: 30px;

  p {
    font-size: 1.5rem;
  }
}
.footer-main__share,
.footer-main__links,
.footer-main__legal {
  top: 3rem;
}
.footer-main__share {
  @include display-flex(flex);
  @include align-items(center);
  @include flex-direction(column);

  margin-bottom: 30px;
  padding: 0 ($column-gutter/2);
  text-align: center;
}
.footer-main__share__facebook,
.footer-main__share__support {
  font-size: 1.25rem;
  padding: 21px 15px 17px;
}

.footer-main__share__facebook {
  @include display-flex(inline-flex);
  @include align-items(center);
  @include justify-content(center);
  display: flex;
  max-height: 46px;
  border: 3px solid #8EB8CC;
  background-color: #E4F1F7;
  padding: 7px 2rem;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
  z-index: 1;

  &::before{
    background-color: rgba(255,255,255,0.5);
    content: '';
    height: 55px;
    width: 100%;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: -2rem;
  }
  &::after {
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    border-bottom: 2px solid #BDD4DF;
    position: absolute;
    top: 2px;
    left: 0;
    content: '';
    height: 38px;
    width: 100%;
  }

  p {
    margin-top: 0;
    margin-right: 12px;
    width: 7rem;
    font-size: 1rem !important;
    margin-bottom: 0;
    z-index: 1;
  }

  nclood-social,
  .fb_iframe_widget,
  .fb_iframe_widget span {
    display: block;
  }
  nclood-social { z-index: 2;}
}

.footer-main__share__support {
  @include display-flex(inline-flex);
  @include align-items(center);

  margin: 0 auto;
}

.footer-main__links {
  margin-bottom: 30px;
  a.esrb-rating {
    min-height: 81px;
  }
}

.footer-main__legal {
  text-align: center;
  min-height: 85px;

  p {
    font: normal 12px Arial, Helvetica, sans-serif;
    margin-bottom: 3px;
  }
  p:last-of-type {
    margin-bottom: 20px;
  }
}

@keyframes cloudAnimation {
  from {
    transform: translateX(-1955px);
  }
  to {
    transform: translateX(0px);
  }
}

.coral-button{
  background-color: get-color('light-coral');
  border: 3px solid get-color('dark-coral');
  padding: 4px 4rem 7px;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  transition: all 0.4s ease;
  white-space: nowrap;
  height: 46px;

  &:hover {
    transform: scale(1.0325);
    transition: all 0.2s ease;
  }

  &::before{
    background-color: rgba(255,255,255,0.1);
    content: '';
    height: 55px;
    width: 100%;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: -2rem;
    z-index: 1;
  }
  &::after {
    background-color: get-color('coral');
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    border-bottom: 2px solid #9E1534;
    position: absolute;
    top: 2px;
    left: 0;
    content: '';
    height: 38px;
    width: 100%;
  }
  span {
    @include font-Dosis();
    color: get-color('white');
    font-size: rem-calc(22px);
    font-weight: 700;
    letter-spacing: 0.5px;
    @include text-Shadow();
    position: relative;
    z-index: 2;
  }
}

@media #{$medium-only} {
  .coral-button {
    padding: 5px 3.5rem;
  }
}
@media #{$medium-up} {
  .footer-main__share {
    @include flex-direction(row);
    @include justify-content(center);
    @include align-items(initial);
  }

  .footer-main__share__support {
    font-size: 1.625rem;
  }

  .footer-main__share__facebook {
    margin-right: 5px;
    margin-bottom: 0;

    p {
      font-size: 1.625rem;
    }
  }

  .footer-main__share__support {
    margin: 0;
  }
}

@media #{$large-up} {
  .footer-main__share__facebook {
    position: relative;
    padding-left: 43px;
    margin-right: 30px;
  }
}

@include reduce-motion {
  .footer-main {
    &::before {
      animation: none;
    }
  }
  
  .coral-button {
    transition: none;
  }
}