.reduce-motion-bar {
  align-items: center;
  background-color: get-color('white');
  display: flex;
  justify-content: center;
  padding: 3px 0 2px;
  position: relative;
  z-index: 30;
}

.reduce-motion-bar a,
.reduce-motion {
  @include font-Dosis;

  appearance: none;
  background-color: transparent;
  border: none;
  border-radius: 0;
  color: #AD2C3D;
  font-weight: 700;
  letter-spacing: 0.055625em;
  padding: 4px 18px;
  text-decoration: none;
  text-transform: uppercase;

  @media #{$reduce-motion-query} {
    display: none;
  }
}

.reduce-motion__label {
  align-items: center;
  display: flex;

  .icon {
    height: 10px;
    margin-left: 8px;
    width: 8px;
  }
}

.reduce-motion__label--off {
  display: none;

  .icon {
    height: 8px;
    width: 8px;
  }
}

@include reduce-motion() {
  .reduce-motion__label--off {
    display: flex;
  }

  .reduce-motion__label--on {
    display: none;
  }
}
