.page-about {

  .button {
    cursor: pointer;
  }

  .bg {
    // height: 100%;
  }
  .pageheader {
    backface-visibility: hidden;
    background-image: linear-gradient(0deg, #CCFFFD 0%, #CBB0FF 100%);

    &.loaded {
      .char2,
      .kirby {
        opacity: 1;
        transform:scale(1) translate3d(0,0,0);
      }
      .char3 {
        opacity: 1;
        transform:scale(1);
      }
    }
    .pageheader__characters {
      display: flex;
      justify-content: center;
      margin-top:25px;
      // margin-bottom:-14px;
      margin-bottom: 15px;
      backface-visibility: hidden;
      transform: translateZ(0);
    }
    .char2 {
      opacity: 0;
      position: absolute;
      text-outline: -2px;
      top: -12%;
      right: 72%;
      width:24%;
      transform:scale(0) translate3d(-950%,30%,0);
      transition: transform .4s ease-out, opacity 0s linear;
    }
    .kirby {
      opacity: 0;
      position: relative;
      width:60%;
      z-index:3;
      transform: scale(0) translate3d(-160%,0,0);
      transition:.4s transform ease-out, opacity 0s linear;
      transition-delay: .25s;
      left: -5%;
    }
    .char3 {
      opacity: 0;
      position: absolute;
      width:26.05421687%;
      top:50%;
      left:64%;
      z-index: 2;

      transform: scale(0);
      transition:.2s transform ease-out, opacity 0s linear;
      transition-delay: .6s;
    }
  }
  .section--characters {
    background-image: linear-gradient(-180deg, #FCD555 0%, #FFAE29 100%);
    padding:60px 0 120px 0;
    position: relative;
  }

  .bg {
    $img:'bg_starpattern-white.png';
    background:image-url($img) repeat center center;
    height:100%;
    left:0;
    opacity: .2;
    position: absolute;
    top:0;
    width:100%;
  }
  .cards {
    position: relative;
    z-index:2;
  }

  // Characters

  .section--characters {
    padding:60px 0 80px 0;
    text-shadow: 0 2px rgba(255, 255, 255, 0.5);

    // * {
    //   outline: solid 1px red;
    // }

    .wrap {
      position: relative;
    }
    
    .feature__image {
      position: relative;
      z-index: 1;
      border: 3px solid #ffffff;
      border-radius: 1.75rem;
      overflow: hidden;
      box-shadow: 4px 4px 0 0 rgba(0, 0, 0, 0.2);
      img {
        display: block;
        max-width:100%;
      }
    }

    .kirby {
      padding: 45px;
      padding-top: 0;

      .char {

        @media #{$small-only} {
          max-width: 400px;
          margin: 0 auto;
        }
      }
    }

    .char {
      left: auto;
      right: -50px;
      transition: transform .25s cubic-bezier(0,.62,0,1.21);
      opacity: 0;
    }

    .character {
      &:nth-of-type(1) .char {
        opacity: 0;
        transform: translateX(-50vw);
      }

      &:nth-of-type(3) .char {
        opacity: 0;
        transform: translateX(50vw);
      }
    }

    .vp-onf-40 .character .char  {
      transform: translateX(0);
      opacity: 1;
    }

    @media #{$small-only} {
      
      .character, .kirby {
        text-align: center !important;
        padding: 0 10vw;

        &.kirby {
          // padding: 0;
          margin-bottom: 30px;
        }

        .char--kirby {
          margin-bottom: 30px;
        }

        .char--meta-knight {
          margin-top: -20px;
        }

        .char--waddle-dee {
          margin-top: -50px;
        }

        .char--king-dedede {
          margin-top: 30px;
          margin-bottom: 10px;
        }
      }
    }

    .characters__image {
      position: relative;

      .char {
        margin: 0 auto;
        max-width: 150px;
        bottom: -70px;
      }
      
    }
    .characters__copy {
      width: 100%; // IE11
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-self: middle;
      max-width: 500px;
    }
  }

  .character {
    padding: 20px;
  }

  .intro--characters {
    text-align: center;
    margin-bottom: 50px;

    p {
      margin-top: 0;
    }

    h2 {
      margin-bottom: 10px;

      @media #{$small-only} {
        font-size: 2.5rem;
      }
    }
  }

  .section--games {
    padding:60px 0 120px 0;
    position: relative;
    $img: 'history-bg-pattern-pink-2x.jpg';
    background-image: image-url($img);
    background-size: 500px 500px;
  }

  .intro--games {
    @include font-Dosis();
    @include shadow;

    max-width: 525px;
    text-align: center;

    p {
      margin-top: 0;
    }

    h2 {
      margin-bottom: 5px;

      @media #{$small-only} {
        font-size: 2.5rem;
      }
    }
  }

  .games {
      transform: scale(1);
      opacity: 1;
      transition: transform .25s ease-in-out, opacity .25s ease-in-out;
      overflow: visible;
      justify-content: stretch;
      padding: 0;
    &.is-hidden {
      transform: scale(.85);
      opacity: 0;
    }
  }

  .games__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    display: none;
    overflow: visible;
    margin: 200px 11px 0px 11px;
    max-width: calc(100% - 22px);

    &:nth-child(-n+3){
      display:flex;
    }

    @at-root .games.is-load-all .games__item {
      display: flex;
    }

    a {
      text-decoration: none;
    }

    .games__title {
      height: auto !important;
    }

    .games__nso {
      background: #fff;
      border-radius: 5px;
      color: #E60012;
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 1px;
      padding: 5px;
    }

    @media #{$medium-only} {
      max-width: calc(33% - 22px);
      &:nth-child(-n+6){
        display:flex;
      }
      .games__copy {
        min-height: 293px;
      }
    }

    @media #{$medium-up} {
      margin: 200px 11px 0px 11px;
      margin-top: 200px;
    }

    @media #{$large-up} {
      max-width: calc(25% - 22px);
      &:nth-child(-n+8){
        display:flex;
      }
    }
  }


  .games__copy {
    @include font-Dosis();
    @include shadow;

    padding: 0px;
    position: relative;
    z-index: 1;
    border: 3px solid #ffffff;
    border-radius: 1rem;
    overflow: visible;
    box-shadow: 4px 4px 0 0 rgba(0, 0, 0, 0.2);
    background: rgba(255,255,255,.5);
    text-align: center;
    margin-bottom: 4px;
    transition: background .15s ease-out;
    
    img {
      display: block;
      max-width:100%;
    }

    span, h5 {
      display: block;
      margin: 15px 0;
      

      &:last-of-type {
        margin-bottom: 0;
      }
    }
    a {
      height: 100%;
      width: 100%;
      &:hover {
        background: rgba(255,255,255,.75);
      }
    }
  }

  .games__copy-content {
    padding: 30px 25px 25px 25px;
  }

  .games__packshot {
    width: 150px;
    position: absolute;
    bottom: 83%;
    z-index: 20;
    left: calc(50% - 75px);
  }

  .games__year {
    font-size: 2.25rem;
    font-weight: 800;
    letter-spacing: 0.055555556em;
  }
  
  .games__platform {
    font-weight: bold;
    color: #AD2C3D;
    font-size: rem-calc(14px);
    letter-spacing: 0.062142857em;
  }

  // .platform-wiiu .games__platform {
  //   color: get-color('cyan');
  // }

  .games__load-all {
    margin-top: 50px;
    
    .button {
      display: block;
      margin-left: auto;
      margin-right: auto;
      max-width: 500px;
      width: 100%;
    }
  }

  .games__title {
    font-size: 1.5rem;
    font-weight: bold;
    font-size: 1.125rem;
    font-weight: 800;
    letter-spacing: 0.055555556em;
  }

  .game-sort {
    @include font-Dosis();
    @include shadow;

    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    height: 40px;
    font-weight: bold;
  }

  .game-sort__label {
    line-height: 2.5rem;
    padding-right: 1rem;
    font-size: 1.125rem;
  }

  .game-sort__toggle {
    appearance: none;
    background: rgba(255,255,255,.50);
    border: 2px solid #ffffff;
    border-radius: 1.75rem;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    height: 100%;
    justify-content: space-around;
    list-style: none;
    margin: 0;
    overflow: hidden;
    padding: 0;
    position: relative;
    width: 175px;
  }

  .game-sort__option {
    text-align: center;
    position: relative;
    z-index: 30;
    font-size: .9rem;
    line-height: 2rem;
    letter-spacing: 0.78px;
    text-shadow: 0 2px 0 rgba(255,255,255,0.50);
    width: 50%;

    span {
      position: relative;
      top: -1px;
    }
  }

  .game-sort__status {
    width: 50%;
    left: 0;
    top: 0;
    position: absolute;
    height: 100%;
    background-color: #fff;
    transform: translateX(0%);
    border-radius: 1.75rem;
    z-index: 29;
    transition: transform .15s ease-in-out;
    
    @at-root .sort-oldest .game-sort__status {
      transform: translateX(100%);
    }
  }
  .customer__support {
    position: relative;
    z-index: 10;
    margin-bottom: 10px;
    margin-top: 60px;
  }
}

@media #{$large-up} {

  .page-about {
    .section--characters {
  
      .feature__image {
        margin-left: 100px;
        background-color: #ff00B7;

        img {
          opacity: 0;
        }
      }

      .row--characters:nth-of-type(odd) {
        flex-direction: row-reverse;
  
        .feature__image {
          margin-right: 100px;
          margin-left: 0;
        }
      }

      .characters__copy {
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }
  }
}

@include reduce-motion {
  .page-about {
    .pageheader__characters {
      .kirby, .char2, .char3 {
        opacity: 0;
        transform: none;
        transition: opacity .4s linear;
      }

      .kirby {
        transition-delay: .25s;
      }

      .char3 {
        transition-delay: .6s;
      }
    }

    .pageheader.loaded {
      .pageheader__characters {
        > * {
          opacity: 1;
          transform: none;
        }
      }
    }

    .section--characters {
      @for $i from 1 through 3 {
        .character:nth-of-type(#{$i}) {
          .char {
            opacity: 0;
            transform: none;
            transition: opacity .3s linear ($i - 1) * .25s;
          }
        }
      }

      .vp-onf-40 {
        .character:nth-of-type(1),
        .character:nth-of-type(2),
        .character:nth-of-type(3) {
          .char {
            opacity: 1;
          }
        }
      }
    }

    .game-sort__status,
    .sort-oldest .game-sort__status {
      transition: none;
    }
  
    .games,
    .games.is-hidden {
      transform: scale(1);
    }
  }
}