.page-videos {
  .pageheader {
    backface-visibility: hidden;
    background-image: linear-gradient(0deg, #F6A8B3 0%, #FDD6BD 100%);

    .flying {

    }
    &.loaded {
      .flying,
      .kirby {
        transform:scale(1) translate3d(0,0,0);
      }
      .bell {
        transform:scale(1);
      }
    }
    .pageheader__characters {
      display: flex;
      justify-content: center;
      margin-top:56px;
      margin-bottom:-14px;
      backface-visibility: hidden;
      transform: translateZ(0);
    }
    .flying {
      position: absolute;
      top:-10%;
      right:-2%;
      width:31.1746988%;
      transform:scale(0) translate3d(-950%,30%,0);
      transition: transform .4s ease-out;

    }
    .kirby {
      position: relative;
      width:52.71084337%;
      // z-index:2;
      transform: scale(0) translate3d(-160%,0,0);
      transition:.4s transform .25s ease-out;
    }
    .bell {
      position: absolute;
      width:26.05421687%;
      top:0;
      left:0;

      transform: scale(0);
      transition:.2s transform .6s ease-out;
    }
  }
  .section--cards {
    background-image: linear-gradient(-180deg, #7641D4 0%, #C57CB1 100%);
    padding:60px 0 120px 0;
    position: relative;
  }

  .bg {
    $img:'bg_starpattern-white.png';

    background:image-url($img) repeat center center;
    height:100%;
    left:0;
    opacity: .2;
    position: absolute;
    top:0;
    width:100%;
  }
  .cards {
    position: relative;
    z-index:2;
  }
}
