.modal {
  z-index: 101;

  &.is-open {
    z-index: 10001; // cover global nav
  }
  .button:hover, .button:focus {
    transform: translateY(-50%);
    // transition-duration: 0.2s;
  }
}

.modal__wrapper {
  border: solid 0px transparent;
}

.modal__content {
  padding: 10px;
  // @extend .fancy-border;
}

.modal--retailer {
  .modal__wrapper {
    width: 90%;
  }

  .modal__content {
    height: 70vh;
    padding: 10px;
    background: white;
  }
}

.modal--video {

  .modal__overlay {
    background: rgba(#032064, .75);
  }
}

.modal__close {
  background-color: get-color("yellow");
  border-radius: 50%;
  display: grid;
  font: 0/0 a;
  grid-template-areas: "main";
  height: 38px;
  margin: 0 0 0 auto;
  overflow:hidden;
  padding: 0;
  place-content: center;
  position: absolute;
  right: 0;
  top: -60px;
  transform: rotate(45deg) scale(1);
  transition: background-color .4s, transform .4s;
  width: 38px;
  z-index: 100;
  z-index: 11500;

  &::before, &::after {
    background-color: get-color("dark-blue");
    content: "";
    grid-area: main;
    height: 3px;
    width: 17px;
  }

  &::after {
    transform: rotate(90deg) scale(1);
  }
}

@media (hover: hover) {
  .modal__close:hover {
    background-color: get-color('light-orange');
    transform: rotate(45deg) scale(1.1);
  }
}

@media #{$small-only} {
  .modal__wrapper {
    max-width: 100%;
    padding: 0;
  }

  .modal__content {
    margin: 0;
    padding: 10px;
    border: none;
  }
}

@media #{$large-up} {
  .modal__wrapper {
    max-width: 900px;
  }
  .modal__close {
    top: -58px;
    right: 8px;
  }
}

@media screen and (min-height: 500px) {
  .modal--retailer {
    .modal__content {
      height: 440px;
      padding: 20px 10px;
    }
  }
}