.page-home {
  section {
    position: relative;
  }

  .hero-wrapper {
    z-index: 2;
  }

  main {
    z-index: 5;
  }
  .section-title {
    @include font-Sniglet();
    font-size: rem-calc(48px);
    font-weight: 800;
  }

  .hero {
    text-align: center;
  }

  .hero .cta {
    margin-top: 20px;
  }

  .hero .starburst {
    max-width: 300px;
    min-width: 300px;
    margin-left: auto;
    margin-right: auto !important;
    display: inline-block;
  }

  .section--ability {
    $img: "bg-starpattern-purple_2x.jpg";
    background-image: image-url($img);
    background-size: 120px 120px;
    padding: 40px 0;
    color: #fff;
    z-index: 2;
    position: relative;
    h3 {
      margin-bottom: 10px;
    }
    h4 {
      margin: 0;
    }
    p {
      color: #fff;
      text-shadow: 0 2px 0 rgba(0, 0, 0, 0.2);
    }
    .img-max {
      display: block;
      margin: 0 auto;
    }
  }

  //30 YEARS
  .section--30-years {
    $img: "anniversary-bg.jpg";
    background: image-url($img) center center;
    background-size: cover;
    padding: 7rem 0;
    .column {
      background: white;
      border-radius: 1.25rem;
      margin-left: 18rem;
      padding: 2.5rem 8rem;
      position: relative;
      .char-30 {
        position: absolute;
        left: -280px;
        top: -40px;
        height: 300px;
        width: 300px;
        img {
          width: 100%;
        }
      }
      .starburst {
        margin: 25px auto 0px auto !important;
        max-width: 300px;
      }
    }
  }

  // NEWS AND VIDEOS
  .section--news-and-videos {
    $img: "star-gradient-bg.jpg";
    background: image-url($img) center center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 5rem 0;
    h2 {
      color: white;
    }
    .starburst {
      margin: 25px auto 0px auto !important;
      max-width: 300px;
    }
    ul {
      padding-left: 0;
    }
  }

  //FEATURE
  .section--feature {
    .bg {
      $img: "bg_starpattern-white.png";
      background: image-url($img) repeat center center;
      z-index: -1;

      height: 100%;
      left: 0;
      opacity: 0.35;
      position: absolute;
      top: 0;
      width: 100%;
    }
    background-color: #ffec40;
    color: get-color("cobalt");
    padding: 5rem 0;
    position: relative;
    z-index: 2;

    .feature__image {
      border: 3px solid #ffffff;
      border-radius: 1.75rem;
      display: block;
      padding: 0;
      position: relative;
      overflow: hidden;
      z-index: 1;

      &:not(:focus) {
        box-shadow: 4px 4px 0 0 rgba(0, 0, 0, 0.2);
      }

      img {
        display: block;
        max-width: 100%;
        width: 100%;
      }
    }

    .feature__video-button {
      position: relative;

      @media (hover: hover) {
        &:hover {
          .play-button {
            transform: translate(-50%, -50%) scale(1.1);
          }

          .play-button__bg {
            opacity: 0.7;
          }
        }
      }

      .play-button {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1);
        transition: transform .4s;
      }

      .play-button__bg {
        opacity: 1;
        transition: opacity .4s;
      }
    }
  

    .feature__text {
      color: #1423a5;
      margin-top: 30px;
    }

    .starburst {
      margin: 25px auto 0px auto !important;
      max-width: 300px;
    }
  }

  //GAMES
  .section--games {
    position: relative;
    padding: 4rem 0 6rem;
    $img: "history-bg-pattern-pink-2x.jpg";
    background: image-url($img);
    background-size: 500px;

    h2 {
      @include shadow;

      font-size: rem-calc(48px);
      margin-bottom: 1.5rem;
    }
    .button__games {
      .starburst {
        max-width: 300px;
        min-width: 300px;
        margin: 30px auto 10px auto !important;
      }
    }
    .games__links {
      @include font-Dosis();
      @include shadow();
      font-size: rem-calc(18px);
      font-weight: 700;
      letter-spacing: 0.5px;
      margin-top: 2rem;
      padding-bottom: 1rem;
      line-height: 1.5;
      .link {
        font-weight: 800;
        text-decoration: none;
        white-space: nowrap;
        display: inline-block;
        transition-duration: 0.3s;
        transition-property: transform;
        transform: translateZ(0);
        &:hover {
          transform: translateY(-3px);
        }
      }
      span {
        padding: 0 0.5em;
        display: none;
        margin-bottom: 2rem;
      }

      span.heading {
        display: inline-block;
      }

      .platform-links {
        text-align: center;
      }

      .platform-link,
      .link {
        display: block;
      }
    }
  }

  //AMIIBO
  .section--amiibo {
    $img: "platform1-top.png";
    $img-height: image-height($img);
    position: relative;
    background: linear-gradient(to bottom, #f0f878 10%, #a2ba30 100%);
    padding: calc(5rem - #{$img-height}) 0 4rem;
    &::before {
      background: image-url($img) top center;
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: $img-height;
      z-index: 1;
    }
    &::after {
      background-image: image-url("platform-1-bg.gif");
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      opacity: 0.5;
    }
    .amiibo__small-characters {
      position: relative;
      top: -5.75rem;
      display: flex;
      justify-content: flex-start;
      z-index: 1;
      .kirby {
        margin-left: 4rem;
        margin-right: 2rem;
        .shadow {
          background: radial-gradient(
            ellipse,
            get-color("shadow") 0%,
            rgba(0, 0, 0, 0) 80%
          );
          height: 10px;
          width: 90px;
          border-radius: 50%;
          margin-left: 0.75rem;
        }
      }
      .baddy {
        align-self: flex-end;
        .shadow {
          background: radial-gradient(
            ellipse,
            get-color("shadow") 0%,
            rgba(0, 0, 0, 0) 80%
          );
          height: 10px;
          width: 60px;
          border-radius: 50%;
          margin-left: 0.5rem;
        }
      }
    }
    .amiibo__content {
      position: relative;
      z-index: 2;
      h2 {
        @include shadow();
        font-size: rem-calc(48px);
        margin-bottom: rem-calc(24px);
      }
    }
    .button-container {
      margin-top: 2rem;
      padding: 0 10px;
      .starburst {
        width: 400px;
        width: 100%;
        max-width: 400px;
      }
    }
    .amiibo__button {
      display: block;
      clear: both;
      position: relative;
      overflow: hidden;
      background-color: get-color("light-coral");
      border: 3px solid get-color("dark-coral");
      padding-top: 4px;
      max-height: 46px;
      height: 46px;
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      text-align: center;
      text-decoration: none;
      z-index: 1;
      transition: all 0.4s ease;
      &:hover {
        transform: scale(1.0325);
        transition: all 0.2s ease;
      }
      &::before {
        background-color: rgba(255, 255, 255, 0.1);
        content: "";
        height: 55px;
        width: 100%;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top: -2.125rem;
        z-index: 1;
      }
      &::after {
        background-color: get-color("coral");
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        border-bottom: 2px solid #d27d7a;
        position: absolute;
        top: 2px;
        left: 0;
        content: "";
        height: calc(100% - 2px);
        width: 100%;
      }
      span {
        @include font-Dosis();
        @include text-Shadow();
        color: get-color("white");
        font-size: rem-calc(22px);
        font-weight: 700;
        letter-spacing: 0.5px;
        position: relative;
        z-index: 2;
        white-space: nowrap;
      }
      img {
        position: absolute;
        left: calc(100% - 35px);
        z-index: 5;
        top: 50%;
        transform: translateY(-50%);
        filter: drop-shadow(0 2px get-color("shadow"));
      }
    }
    //MY NINTENDO
    .section--my-nintendo {
      padding: 7rem 0;
      position: relative;
      z-index: 2;
      .column {
        background: white;
        border-radius: 1.25rem;
        margin-left: 18rem;
        padding: 2.5rem 8rem;
        position: relative;
        .char-30 {
          position: absolute;
          width: 364px;
          height: 364px;
          left: -280px;
          top: -40px;
          img {
            width: 100%;
          }
        }
        .starburst {
          margin: 25px auto 0px auto !important;
          max-width: 300px;
        }
      }
    }
  }

  //PLAY
  .section--play {
    position: relative;
    height: 100%;
    background: linear-gradient(to bottom, #fcd555 10%, #ffae29 100%);
    &::after {
      background-image: image-url("star-single.png");
      background-size: 4.4rem;
      opacity: 0.5;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      min-height: 750px;
      content: "";
    }
  }

  //NEWS and VIDEOS
  .section--news-videos {
    $img: "patterns/platform2-top-tile.jpg";
    background: get-color("blue") image-url($img) repeat-x left top;
    color: get-color("white");
    padding: 6.75rem 0 4rem;
    text-align: center;
    position: relative;
    h3 {
      letter-spacing: 1px;
      text-shadow: 0 2px 0 rgba(0, 0, 0, 0.2);
    }
    .characters {
      $img: "home/char-baddy.png";
      width: 100%;
      height: image-height($img);
      position: absolute;
      bottom: 100%;
      left: 0;
    }
    .baddy {
      $img: "home/char-baddy.png";
      position: absolute;
      top: 0;
      width: image-width($img);
      height: image-height($img);
      right: 18%;
      &:before {
        content: "";
        animation: 4s charHover infinite;
        background: image-url($img) no-repeat center center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
      &:after {
        content: "";
        animation: 4s charShadow infinite;
        position: absolute;
        top: 114%;
        background: rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        width: 50px;
        height: 4px;
        left: 50%;
        margin-left: -25px;
      }
    }
  }

  //FACEBOOK
  .section--facebook {
    color: #fff;
    padding-bottom: 2.5rem;
    background: #c4cfe2;
    .facebook__content {
      position: relative;
      max-width: 800px;
      margin: 0 auto;
      padding: 0 11px;
    }
    .facebook__kirby {
      padding-right: 8px;
    }
    .facebook__kirby img {
      display: inline-block;
      transition: transform 0.2s ease;
      margin-top: -25px;
    }
    .facebook__text {
      position: relative;
    }
    .facebook__text h3 {
      margin: 0;
      padding: 0 10px;
      letter-spacing: 1px;
      text-shadow: 0 2px 0 rgba(0, 0, 0, 0.2);
      a {
        color: #324c95;
        text-decoration: none;
      }
    }

    &:before {
      $img: "patterns/scallop-edge-2x.png";
      background: image-url($img) repeat-x left top;
      background-size: image-width($img) / 2 image-height($img) / 2;
      content: "";
      width: 100%;
      height: image-height($img) / 2;
      position: absolute;
      top: -14px;
      left: 0;
    }
    &:hover {
      .facebook__kirby img {
        transition-duration: 0.3s;
        transform: translateY(-10px);
      }
    }
  }
}

@media #{$small-only} {
  .page-home {
    h2 {
      font-size: 2.5rem !important;
    }
    &__image-group {
      top: 7rem;
      height: 10rem;
      width: 275px;
      margin-left: 50%;
      transform: translateX(-50%);

      .kirby {
        width: 215px;
        height: auto;
        top: -10rem;
        left: 3.5rem;
      }
      .metaknight {
        width: 150px;
        height: auto;
        top: -15rem;
        left: 9rem;
      }
      .dedede {
        width: 108px;
        height: auto;
        top: -7rem;
        left: -1.5rem;
      }
      .waddledee {
        width: 50px;
        height: auto;
        top: -10.5rem;
        left: 2rem;
      }
    }

    .section--30-years {
      padding: 5rem 0rem;
      .wallpaper {
        margin-bottom: 2rem;
      }
      .txt {
        padding: 4rem 0.5rem 2.5rem 0.5rem;
      }
    }

    .section--30-years .column {
      margin: 200px 1rem 0rem 1rem;
      padding: 2.5rem 1.5rem;
      .char-30 {
        left: -30px;
        right: 0;
        margin: auto;
        top: -250px;
      }
    }

    .section--feature {
      .feature__video-button {
        @media (hover: hover) {
          &:hover {
            .play-button {
              transform: translate(-50%, -50%) scale(.8);
            }
          }
        }
  
        .play-button {
          transform: translate(-50%, -50%) scale(.6);
        }
      }

      p {
        font-size: 1rem;
        margin-right: 0 !important;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .section--games {
      .games__game-links .link {
        display: -webkit-inline-box;
      }
    }
    .section--amiibo {
      .button-container {
        display: flex;
        justify-content: center;
      }
    }
  }
}

@media #{$medium-up} {
  .page-home {
    .section--feature {
      padding: 5.375rem 0 3.5rem;
    }
    .section--facebook {
      .facebook__content {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .facebook__kirby img {
        margin-bottom: -35px;
      }
      .facebook__text {
        max-width: 440px;
      }
    }
  }
}

@media #{$medium-only} {
  .page-home .section--30-years .column {
    margin-right: 30px;
    padding: 1.5rem 3.5rem;
    h3 {
      margin-top: 0px;
    }
  }
  .char-30 {
    top: 0px;
    left: -250px;
  }
}

@media #{$large-only} {
  .page-home .section--30-years .column {
    margin-right: 30px;
  }
  .wallpaper {
    margin-bottom: 50px;
  }
}

@media #{$large-up} {
  .page-home {
    .home-hero {
      height: 700px;
    }
    .home-hero__overlay {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 100%;
      left: 0;
    }
    .hero__links {
      position: absolute;
      bottom: 0;
      width: 100%;
      padding: 80px 0;
    }
    .hero__button {
      padding-bottom: 0;
    }

    .section--games {
      .games__links {
        padding-bottom: 0;
      }
    }

    .section--facebook {
      .facebook__text {
        max-width: none;
      }
    }

    .section--games .games__links {
      span {
        padding: 0 0.5em;
        display: inline;
      }

      .platform-links {
        text-align: center;
      }

      .platform-link,
      .link {
        display: inline;
      }

      .starburst {
        margin-bottom: 0;
      }
    }
  }
}

@media #{$xlarge-up} {
  .page-home {
    .section--games {
      padding-top: 4rem;
    }
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .page-home {
    .section--amiibo {
      .amiibo__small-characters {
        top: -3.75rem;
      }
    }
  }
}

@include reduce-motion {
  .page-home {
    .section--games .games__links {
      .BSParticle {
        display: none;
      }
    }
  }

  .section--feature {
    .feature__video-button {
      .play-button,
      .play-button__bg {
        transition: none;
      }
    }
  }
}
