.news-list,
.videos-list {
  padding:0;
}
.news-list {
  .card-wrapper {
    flex: 1 1 50%;
    max-width:50%;
    padding:0 11px;
    width: 50%;

  }
}
.videos-list {
  position: relative;
  margin-top:50px;
  .card-wrapper {
    padding:0 11px;
  }

}

.cards {
  align-items: stretch;
  display: flex;
  flex-wrap:wrap;
  justify-content: center;
  text-align: left;
  &.animate {
    .card-wrapper {
      opacity: 0;
      transition:opacity .2s ease, transform .25s ease;
    }
    &.vp-on-30 {
      .card-wrapper {
        opacity: 1;
        transform:translate(0,0);
      }
    }
  }
}
.cards-cta {
  margin:35px auto 0;
  max-width:560px;
  width:90%;
}
.card-wrapper {
  align-items: stretch;
  display: flex;
  padding:11px;
}
.card {
  background: #FFFFFF;
  border-radius: 20px;
  border: 3px solid #FFFFFF;
  box-shadow: 4px 4px 0 0 rgba(0,0,0,0.20);
  display: block;
  padding-bottom:10px;
  text-decoration: none;
  width:100%;

  &:hover {
    .card__header img {
      transform:scale(1.05);
    }
  }
}

.card__header {
  border-top-right-radius:18px;
  border-top-left-radius:18px;
  overflow: hidden;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  img {
    display: block;
    transition:transform .2s ease;
    width:100%;
  }
}
.card__content {
  color: get-color('cobalt');
  font-size:1.125rem;
  padding:12px 20px;
  time {
    @include font-Dosis();

    display: inline-block;
    font-size:0.7777777778em;
    font-weight:700;
    margin-bottom:7px;
  }
  h5,p {
    @include font-Dosis();
    font-size:1rem;
    line-height: 1.3333333333;
  }
  h5 {
    margin:0 0 10px 0;
    font-weight:900;
    letter-spacing: 1px;
  }
}


.card--video {
  &:hover {
    cursor: pointer;
    .icon--play {
      &:after {
        transform:scale(1.1);
        transition-duration: .1s;
      }
    }
  }
  .card__header {
    position: relative;
  }

  .icon {
    background:#D27D7A;
    border-radius:50%;
    border:solid 4px #AD2C3D;
    height:71px;
    left:50%;
    margin-left:-35.5px;
    margin-top:-35.5px;
    overflow: hidden;
    position:absolute;
    top:50%;
    width:71px;
    z-index:2;
    &:before {
      background:#F6CDCC;
      content:'';
      height:50%;
      left:0;
      position: absolute;
      top:0;
      width:100%;


    }
    .icon--play {
      background: #F49B98;
      border-radius:50%;
      border:none;
      height:56px;
      left:50%;
      margin-left:-33.5px;
      margin-top:-28px;
      position: absolute;
      top:50%;
      width:67px;
      &:before {
        background-color: rgba(255,255,255,0.2);
        border-radius:50%;
        bottom:50%;
        content:'';
        height:100%;
        left:0;
        position: absolute;
        width:100%;
      }
      &:after {
        $img:'icon-play--white.svg';

        background:image-url($img) no-repeat center center;
        content:'';
        height:image-height($img);
        left:52%;
        margin-left:image-width($img)/2 * -1;
        margin-top:image-height($img)/2 * -1;
        position: absolute;
        top:50%;
        transition:transform .3s ease;
        width:image-width($img);
      }
    }
  }
  .card__content {
    text-align: center;
  }
}



@media #{$medium-only} {
  .animate {
    .card-wrapper {
      transform:translate(-40%,0);
      &:nth-of-type(2n+2) {
        transform:translate(40%,0);
      }
    }
  }
}

@media #{$xsmall-only} {
  .news-list {
    .card-wrapper {
      flex-basis:100%;
      width:100%;
      max-width:100%;
      margin-bottom:15px;
    }
  }
}
@media #{$medium-up} {
  .card__content {
    h5,p {
      font-size:1.125rem;
    }
  }
}
@media #{$large-up} {
  .animate {
    .card-wrapper {
      transform:translate(-40%,0);
      &:nth-of-type(3n+2) {
        transform:translate(0,30%);
      }
      &:nth-of-type(3n+3) {
        transform:translate(40%,0);
      }
    }
  }

  .videos-list {
    margin-top:0;
    .cards-cta {
      bottom:0;
      left:50%;
      position:absolute;
      transform:translateX(-50%);
    }
  }
}
