.border {
  &__zig-zag {
    &::before,
    &::after {
      z-index: 5;
      $img: 'zig-zag-border.png';
      position: absolute;
      background: image-url($img);
      width: 100%;
      height: image-height($img);
      content: '';
      left: 0;
    }
    &::before {
      top: -1rem;
    }
    &::after {
      bottom: -1rem;
    }
  }

  &__zig-zag-top {
    &::before {
      $img: 'zig-zag-border.png';
      position: absolute;
      background: image-url($img);
      width: 100%;
      height: image-height($img);
      content: '';
      left: 0;
      top: -1rem;
      z-index: 3;
    }
  }

  &__wave-top{
    &::before {
      $img: 'wave-border.png';
      position: absolute;
      background: image-url($img);
      width: 100%;
      height: image-height($img);
      content: '';
      left: 0;
      top: -1.5rem;
      filter: drop-shadow(0 -10px 6px rgba(0, 0, 0, 0.2));
    }
  }
}
